import React from "react";
import { Link, useNavigate } from "react-router-dom";
import { Box } from "theme-ui";
import { ApplicationRoutes } from "../../Types";
import { useGetData } from "../../Hooks";

interface IProfiles {
  fullScreenRef?: React.RefObject<HTMLDivElement>;
}

export const ProfileSelector: React.FC<IProfiles> = ({ fullScreenRef }) => {
  const { getProfiles } = useGetData();
  const navigate = useNavigate();

  const handleChooseProfile = async (profileId: string) => {
    // fullScreenRef?.current?.requestFullscreen();
    navigate(ApplicationRoutes.ProfileLink + profileId);
  };

  return (
    <Box>
      <Box>
        <h1>
          <Link to={ApplicationRoutes.Home}>Home</Link>{" "}
        </h1>
        <h1>
          <Link to={ApplicationRoutes.ProfileSelector}>Profiles</Link>{" "}
        </h1>
      </Box>

      <Box>
        {getProfiles.map(profile => (
          <h1
            key={profile._id}
            onClick={() => handleChooseProfile(profile._id)}
          >
            {profile.profileName}
          </h1>
        ))}
      </Box>
    </Box>
  );
};
