import io from "socket.io-client";

type SocketServicesRefreshCallBack = () => void;

type SocketServicesActions = {
  action: string;
  deviceId: string;
  subAction: string;
  pageNumber?: number;
  profile?: string;
};

type SocketServicesActionsCallBack = (
  data: SocketServicesActions
) => Promise<void>;

enum SocketServicesSubscriptions {
  ApplicationRefresh = "macroDeckerSocketAutoUpdate",
  Actions = "macroDeckerSocket"
}

const socketString =
  process.env?.REACT_APP_SOCKET || `http://${window.location.host}`;
const socket = io(socketString);

const socketServices = {
  subscribeApplicationRefresh(callback: SocketServicesRefreshCallBack) {
    socket.on(SocketServicesSubscriptions.ApplicationRefresh, () => callback());
  },

  unSubscribeApplicationRefresh() {
    socket.removeAllListeners(SocketServicesSubscriptions.ApplicationRefresh);
  },

  subscribeActions(callback: SocketServicesActionsCallBack) {
    socket.on(
      SocketServicesSubscriptions.Actions,
      (data: SocketServicesActions) => callback(data)
    );
  },

  unSubscribeActions() {
    socket.removeAllListeners(SocketServicesSubscriptions.Actions);
  }
};

export default socketServices;
