import React from "react";
import { AppRouter } from "./AppRouter";
import { MDThemeProvider } from "./Theme/ThemeProvider";
import { useGetData } from "./Hooks";

function App() {
  const { dataUrls, getStartUpData, setApiString } = useGetData();
  const { apiUrl } = dataUrls;

  React.useEffect(() => {
    setApiString();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    if (!apiUrl) return;
    getStartUpData && getStartUpData();
  }, [apiUrl, getStartUpData]);

  React.useEffect(() => {
    let wakeLock: any = null;

    const requestWakeLock = async () => {
      try {
        wakeLock = await navigator.wakeLock.request("screen");
      } catch (err) {
        console.error("Error requesting wake lock:", err);
      }
    };

    const releaseWakeLock = () => {
      if (wakeLock !== null) {
        wakeLock.release();
        wakeLock = null;
      }
    };

    requestWakeLock();

    return () => {
      releaseWakeLock();
    };
  }, []);

  return apiUrl ? (
    <MDThemeProvider>
      <AppRouter />
    </MDThemeProvider>
  ) : (
    <h1>null</h1>
  );
}

export default App;
