import React from "react";
import { Box, Flex } from "theme-ui";
import axios from "axios";
import { ButtonPads } from "../../Types";
import MacroDeckIcon from "./IconParser";
import { useGetData } from "../../Hooks";

interface IProfileButtonPad {
  buttonPad: ButtonPads;
}

export const ProfileButtonPad: React.FC<IProfileButtonPad> = ({
  buttonPad
}) => {
  const { dataUrls } = useGetData();
  const { apiUrl } = dataUrls;

  const handleButtonPress = async (buttonId: string) => {
    try {
      const endPoint = `${apiUrl}mobile/actions`;
      await axios.post(endPoint, {
        _id: buttonId,
        action: "buttonPad",
        deviceId: "state.deviceId"
      });
    } catch (error) {
      console.error(41, error);
    }
  };

  return (
    <Box
      onClick={
        buttonPad.pageId ? () => handleButtonPress(buttonPad._id) : () => null
      }
      sx={{
        justifyContent: "center",
        alignItems: "center",
        position: "relative",
        background: buttonPad?.bgColor || "#232323",
        color: buttonPad?.textColor || "#232323"
      }}
    >
      <Flex
        sx={{
          justifyContent: "center",
          alignItems: "center",
          position: "absolute",
          top: 0,
          left: 0,
          zIndex: 1,
          height: "100%",
          width: "100%"
        }}
      >
        {buttonPad.icon && (
          <MacroDeckIcon
            icon={buttonPad.icon || "home"}
            color={buttonPad?.iconColor}
          />
        )}
      </Flex>

      <Flex
        sx={{
          justifyContent: "center",
          alignItems: buttonPad.icon ? "flex-end" : "center",
          position: "absolute",
          top: 0,
          left: 0,
          zIndex: 2,
          height: "100%",
          width: "100%",
          paddingBottom: ".5rem"
        }}
      >
        {buttonPad?.text}
      </Flex>
    </Box>
  );
};
