import React from "react";
import { useAppSettingsDataStore } from "../../DataStore/AppDataStore";
import { Profile } from "../../Types";

type TFetchData = () => Promise<unknown>;
type SetApiString = () => void;
type GetProfiles = () => Profile[];
type GetProfile = (id: string) => Profile | undefined;
type DataUrls = { apiUrl: string; socketUrl: string };

interface IUseGetData {
  getStartUpData: TFetchData;
  setApiString: SetApiString;
  getProfiles: Profile[];
  getProfile: GetProfile;
  dataUrls: DataUrls;
  refreshApplication: any;
}

const useGetDataHook = (): IUseGetData => {
  const { fetchMdProfiles, mdProfiles, socketUrl, apiUrl } =
    useAppSettingsDataStore(state => state);

  const getStartUpData = React.useCallback(async () => {
    fetchMdProfiles && (await fetchMdProfiles());
  }, [fetchMdProfiles]);

  const setApiString: SetApiString = () => {
    const { host, port } = window.location;

    if (host && port) {
      const apiString = process.env?.REACT_APP_API || `http://${host}/api/v1/`;
      const socketString = process.env?.REACT_APP_SOCKET || `http://${host}`;
      useAppSettingsDataStore.setState({ apiUrl: apiString });
      useAppSettingsDataStore.setState({ socketUrl: socketString });
    }
  };

  const getProfiles = mdProfiles || [];

  const getProfile: GetProfile = (id: string) => {
    return mdProfiles.find(profile => profile._id === id);
  };

  const dataUrls: DataUrls = { apiUrl, socketUrl };

  const refreshApplication = async () => {
    if (!fetchMdProfiles) return;
    fetchMdProfiles();
  };

  return {
    getStartUpData,
    setApiString,
    getProfiles,
    getProfile,
    dataUrls,
    refreshApplication
  };
};

export default useGetDataHook;
