import React from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Box } from "theme-ui";
import { useGetData } from "../../Hooks";
import { ApplicationRoutes, Profile } from "../../Types";
import { MDPage } from "./Page";
import socketServices from "../../Services/socketServices";

interface IProfileProps {
  fullScreenRef?: React.RefObject<HTMLDivElement>;
  openMenu: () => void;
}

export const MDProfile: React.FC<IProfileProps> = ({
  fullScreenRef,
  openMenu
}) => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { getProfile, getProfiles } = useGetData();
  const [activeProfile, setActiveProfile] = React.useState<Profile>(
    {} as Profile
  );

  const scrollerContainerRef: any = React.useRef(null);

  const mdGotoPage = (pageNumber: number) => {
    if (pageNumber === -1) return;

    const left = scrollerContainerRef.current.clientWidth * (pageNumber - 1);
    console.log({ left });

    scrollerContainerRef.current.scrollTo({
      left,
      behavior: "smooth"
    });
  };

  React.useEffect(() => {
    socketServices.subscribeActions(async data => {
      console.log("subscribeActions", data);

      switch (data.subAction) {
        case "mdHome":
          navigate(ApplicationRoutes.Home);
          break;

        case "mdSettings":
          openMenu();
          break;

        case "mdProfileSelector":
          navigate(ApplicationRoutes.ProfileSelector);
          break;

        case "mdReset":
          window.location.reload();
          break;

        case "mdPage":
          data?.pageNumber &&
            data?.pageNumber > 0 &&
            mdGotoPage(data.pageNumber);
          break;

        case "mdProfile":
          if (!data.profile || data.profile === activeProfile._id) return;
          navigate(`${ApplicationRoutes.ProfileLink}${data.profile}`);
          break;

        case "mdFullScreen":
          if (!fullScreenRef?.current) return;
          fullScreenRef?.current?.requestFullscreen();
          break;

        case "mdFsToggle":
          if (!fullScreenRef?.current) return;

          if (document.fullscreenElement) {
            document.exitFullscreen();
          } else {
            fullScreenRef.current.requestFullscreen();
          }
          break;

        case "mdFsClose":
          document.exitFullscreen();
          break;
      }
    });

    return () => {
      socketServices.unSubscribeActions();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    let stillMounted = true;
    if (!id) return;

    const profileData = getProfile(id);
    if (stillMounted && profileData) {
      setActiveProfile(profileData);
    }

    return () => {
      stillMounted = false;
    };
  }, [id, getProfile, getProfiles]);

  React.useEffect(() => {
    let stillMounted = true;
    if (!id) return;

    const profileData = getProfile(id);

    if (stillMounted && profileData) {
      setActiveProfile(profileData);
    }

    return () => {
      stillMounted = false;
    };
  }, [id, getProfile]);

  return (
    <>
      <Box
        ref={scrollerContainerRef}
        data-testid="profile"
        style={{
          height: "100vh",
          width: "100vw",
          overflowX: "scroll",
          overflowY: "hidden",
          scrollSnapType: "x mandatory"
        }}
      >
        {activeProfile.pages?.map(page => (
          <MDPage
            key={page._id}
            buttonPadCount={activeProfile.buttonPads}
            buttonPads={page.buttonPads}
          />
        ))}
      </Box>
    </>
  );
};
