import React from "react";
import { Box, Grid } from "theme-ui";
import { ButtonPads } from "../../Types";

import { ProfileButtonPad } from "./ButtonPad";

interface IMdPage {
  buttonPadCount: number;
  buttonPads: ButtonPads[];
}

interface IButtonLayoutMap {
  [key: number]: {
    rows: number;
    columns: number;
  };
}

export const MDPage: React.FC<IMdPage> = ({ buttonPadCount, buttonPads }) => {
  const [canvasHeight, setCanvasHeight] = React.useState<number>(0);
  const [isLandScape, setIsLandScape] = React.useState<boolean>(false);

  const buttonLayoutMap: IButtonLayoutMap = {
    6: { rows: 3, columns: 2 },
    8: { rows: 4, columns: 2 },
    12: { rows: 4, columns: 3 },
    15: { rows: 5, columns: 3 },
    24: { rows: 6, columns: 4 },
    32: { rows: 8, columns: 4 }
  };

  React.useEffect(() => {
    const setDimensions = () => {
      setCanvasHeight(window.innerHeight);
      setIsLandScape(window.innerWidth > window.innerHeight);
    };

    setDimensions();

    window.addEventListener("resize", setDimensions);

    return () => {
      window.removeEventListener("resize", setDimensions);
    };
  }, []);

  return (
    <Box
      data-testid="page"
      sx={{
        height: canvasHeight + "px",
        width: "100vw",
        display: "inline-block",
        scrollSnapAlign: "center",
        color: "white"
      }}
    >
      <Grid
        sx={{
          gap: ".375rem",
          gridTemplateColumns: `repeat(${
            buttonLayoutMap[buttonPadCount][isLandScape ? "rows" : "columns"]
          }, 1fr)`,
          height: canvasHeight + "px"
        }}
      >
        {buttonPads.map((buttonPad, i) => (
          <ProfileButtonPad key={buttonPad._id} buttonPad={buttonPad} />
        ))}
      </Grid>
    </Box>
  );
};
