import { create, StoreApi } from "zustand";
import axios from "axios";
import { Profile } from "../Types";

export interface IAppSettingsDataStore {
  apiUrl: string;
  fetchMdProfiles: () => Promise<any>;
  mdProfiles: Profile[];
  socketUrl: string;
}

export const useAppSettingsDataStore = create<IAppSettingsDataStore>(
  (
    set: StoreApi<IAppSettingsDataStore>["setState"],
    get: StoreApi<IAppSettingsDataStore>["getState"]
  ) => ({
    apiUrl: "",
    mdProfiles: [],
    socketUrl: "",

    fetchMdProfiles: async () => {
      try {
        const endPoint = `${get().apiUrl}mobile/start`;
        const { data } = await axios.get(endPoint);

        set({ mdProfiles: data });
      } catch (error) {
        console.error(error);
      }
    }
  })
);
