import React from "react";
import { Box, Grid } from "theme-ui";
import { useGetData } from "../../Hooks";
import { useNavigate } from "react-router-dom";
import { ApplicationRoutes } from "../../Types";

interface IApplicationMenu {
  closeMenu: () => void;
  isMenuOpen: boolean;
  fullScreenRef?: React.RefObject<HTMLDivElement>;
}
export const ApplicationMenu: React.FC<IApplicationMenu> = ({
  closeMenu,
  isMenuOpen,
  fullScreenRef
}) => {
  const [isFullScreen, setIsFullScreen] = React.useState<boolean>(false);
  const { getProfiles } = useGetData();
  const navigate = useNavigate();

  React.useEffect(() => {
    const fullScreenRefCurrent = fullScreenRef?.current;
    if (!fullScreenRefCurrent) return;

    fullScreenRef.current.addEventListener("fullscreenchange", () => {
      setIsFullScreen(!isFullScreen);
    });

    return () => {
      fullScreenRefCurrent?.removeEventListener("fullscreenchange", () => {
        setIsFullScreen(!isFullScreen);
      });
    };
  }, [fullScreenRef, isFullScreen]);

  const handleFullScreenToggle = () => {
    if (!fullScreenRef?.current) return;

    if (document.fullscreenElement) {
      document.exitFullscreen();
    } else {
      fullScreenRef.current.requestFullscreen();
    }
  };

  const handleResetApplication = () => {
    window.location.reload();
  };

  const handleChooseProfile = async (profileId: string) => {
    navigate(ApplicationRoutes.ProfileLink + profileId);
    closeMenu();
  };

  return (
    <Box
      sx={{
        position: "absolute",
        top: isMenuOpen ? "0px" : "-100vh",
        height: "100vh",
        width: "100vw",
        background: "#232323",
        zIndex: 100,
        transition: "top 0.25s ease-in-out",
        borderBottom: "1px solid #333",
        boxShadow: "0px 0px 10px 0px rgba(0,0,0,0.75)",
        padding: "1rem"
      }}
    >
      <Grid sx={{ gridTemplateColumns: "1fr 1fr" }}>
        <Box>
          <h1 style={{ marginBottom: "3rem" }} onClick={handleFullScreenToggle}>
            {isFullScreen ? "Exit Fullscreen" : " Fullscreen"}
          </h1>
          <h1 style={{ marginBottom: "3rem" }} onClick={handleResetApplication}>
            Reset Application
          </h1>
          <h1 style={{ marginBottom: "3rem" }} onClick={closeMenu}>
            Close
          </h1>
        </Box>

        <Box>
          <h1>Select Profile</h1>

          <Box>
            {getProfiles.map(profile => (
              <h1
                key={profile._id}
                onClick={() => handleChooseProfile(profile._id)}
              >
                {profile.profileName} ({profile.buttonPads})
              </h1>
            ))}
          </Box>
        </Box>
      </Grid>
    </Box>
  );
};
