import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { ApplicationRoutes } from "../Types";
import { Home } from "./../Components/Home/Home";
import { ProfileSelector } from "../Components/ProfileSelector/ProfileSelector";
import { MDProfile } from "../Components/Profile/Profile";
import { ApplicationMenu } from "../Components/Menu/Menu";
import socketServices from "../Services/socketServices";
import { useGetData } from "../Hooks";

export const AppRouter: React.FC<{}> = () => {
  const fullScreenRef = React.useRef<HTMLDivElement>(null);
  const [isMenuOpen, setIsMenuOpen] = React.useState<boolean>(false);
  const { refreshApplication } = useGetData();

  React.useEffect(() => {
    socketServices.subscribeApplicationRefresh(() => {
      refreshApplication && refreshApplication();
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    let touchStartX = 0;
    let touchEndX = 0;

    let touchStartY = 0;
    let touchEndY = 0;

    let swiping = false;
    const distanceTheshold = 100;

    function handleTouchStart(event: TouchEvent) {
      touchStartX = event.touches[0].clientX;
      touchStartY = event.touches[0].clientY;
      swiping = false;
    }

    function handleTouchMove(event: TouchEvent) {
      touchEndX = event.touches[0].clientX;
      touchEndY = event.touches[0].clientY;
      swiping = true;
    }

    function handleTouchEnd() {
      const deltaX = Math.abs(touchEndX - touchStartX);
      const deltaY = Math.abs(touchEndY - touchStartY);
      if (!swiping || deltaY < distanceTheshold || deltaX > deltaY) return;

      if (touchEndY > touchStartY) {
        setIsMenuOpen(true);
      } else if (touchEndY < touchStartY) {
        setIsMenuOpen(false);
      }
    }

    document.addEventListener("touchstart", handleTouchStart, false);
    document.addEventListener("touchmove", handleTouchMove, false);
    document.addEventListener("touchend", handleTouchEnd, false);

    return () => {
      document.removeEventListener("touchstart", handleTouchStart);
      document.removeEventListener("touchmove", handleTouchMove);
      document.removeEventListener("touchend", handleTouchEnd);
    };
  }, []);

  return (
    <div ref={fullScreenRef}>
      <BrowserRouter>
        <ApplicationMenu
          closeMenu={() => setIsMenuOpen(false)}
          isMenuOpen={isMenuOpen}
          fullScreenRef={fullScreenRef}
        />

        <Routes>
          <Route path="/" element={<Home />} />
          <Route path={ApplicationRoutes.Home} element={<Home />} />
          <Route
            path={ApplicationRoutes.ProfileSelector}
            element={<ProfileSelector fullScreenRef={fullScreenRef} />}
          />
          <Route
            path={ApplicationRoutes.Profile}
            element={
              <MDProfile
                fullScreenRef={fullScreenRef}
                openMenu={() => setIsMenuOpen(true)}
              />
            }
          />
        </Routes>
      </BrowserRouter>
    </div>
  );
};
